/* styling the Contact-Form */
.contact-container{
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #121212;
    color: #FFF;    
}

  
  .title  {
    margin-bottom: 14px;
    
  }
  
  .form  {
    max-width: 600px;
    width: 100%;    
    display: flex;
    flex-direction: column;
  }
  
  .input  {
    margin-bottom: 14px;
    height: 34px;
    border-radius: 4px;
    border: 0;
    padding: 0 8px;
  }

  
  .textarea{
    margin-bottom: 14px;
    border-radius: 4px;
    border: 0;
    padding: 8px;
    height: 94px;
    resize: none;
  }
  

  .button  {
    height: 34px;
    border-radius: 4px;
    border: 0;
    cursor: pointer;
    background-color: #0fdbff;
    font-size: 18px;    
    transition: background-color, -webkit-transform 0.8s;    
    transition: background-color, transform 0.8s;    
    transition: background-color, transform 0.8s, -webkit-transform 0.8s;    
  }
  

  .button:hover{
    background-color: #0fe3ff;
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
  }


